.friendMessage {
    border: #7018dc solid 1px;
    display:flex;
    flex-direction: column;
}

.friendImageAndText {
    display:flex;

}

.friendImageAndText img {
    width: 48px;
    height: 48px;
}

.friendText {
    background-color:#F5F7FB;
    color:#000000;
    font-weight: 400;
    border-radius: 5px 5px 5px 5px;

}


.friendText:after {

}

.friendName {
    font-size: 18px;
    font-weight: 600;
}

.friendMessageText {
    font-size: 16px;
    border: #3a205b solid 1px;

}

.friendTime {
    font-size: 10px;
    font-weight: 600;
    margin-left: 15px;

}
