.message {
    border: #a01818 solid 1px;
    margin: 10px;
    text-align: end;
    display: flex;
    flex-direction: column;

}

.imageAndText {

    border: #3a205b solid 1px;
    display: flex;
    flex-direction: row-reverse;


}

.imageAndText img {
    width: 48px;
    height: 48px;

}

.text {

    background-color: #0066CC;
    color: #FFFFFF;
    font-weight: 400;
    border-radius: 5px 5px 5px 5px;

}

.text:after {

}

.name {

    font-size: 18px;
    font-weight: 600;
}

.messageText {
    font-size: 16px;
    border: #3a205b solid 1px;


}

.time {
    font-size: 10px;
    font-weight: 600;
    margin-right: 15px;


}